@import "style/mixins.module.scss";

.root {
    display: flex;
    height: 100vh;
}

.header {
    height: calc(100% - 120px);

    @include respond-to("laptop") {
        display: flex;
        align-items: center;
    }
}

.title {
    margin-bottom: 70px;
}

.content {
    max-width: 560px;
    padding: 24px;

    @include respond-to("laptop") {
        padding: 24px 40px;
    }
}

.picture-wrapper {
    min-width: 100%;
    margin-left: -25px;
    margin-right: -25px;
    overflow: hidden;
    position: relative;

    @include respond-to("laptop") {
        display: none;
    }
}

.picture {
    height: 245px;
    min-width: 100%;
    margin-bottom: 40px;
    object-fit: cover;
}

.background {
    display: none;

    @include respond-to("laptop") {
        display: block;
    }
}

.logo {
    display: block;
    width: 160px;
    height: 32px;
    margin: 8px 0 80px;

    background: url("/special/images/icons/s2i-logo.svg") no-repeat left bottom;
    background-size: contain;
}

.column {
    @include respond-to("laptop") {
        min-height: 100vh;
        width: 50%;

        &_content {
            display: flex;
            justify-content: flex-end;
            padding-right: 64px;
        }
    }
}
